/* src/styles/App.module.css */

/* Estilo global do body */
body {
  background-color: #5d1d7e; /* Cor roxa para o fundo de toda a página */
  display: flex;
  justify-content: center; /* Centraliza o conteúdo na horizontal */
  align-items: center; /* Centraliza o conteúdo na vertical */
  height: 100vh; /* Faz o body ocupar a altura total da tela */
  margin: 0; /* Remove margens padrões do body */
  font-family: Arial, sans-serif; /* Define uma fonte para todo o site */
  color: white; /* Cor do texto em branco */
}

/* Container que envolve todo o conteúdo */
.container {
  width: 100%; /* A largura ocupa toda a tela */
  max-width: 1000px; /* Largura máxima para o conteúdo */
  padding: 20px;
  background-color: #1a1a1d; /* Cor de fundo para o conteúdo */
  border-radius: 12px; /* Bordas arredondadas */
  box-shadow: 0 4px 10px rgba(0, 0, 0, 0.2); /* Sombra para dar um efeito de profundidade */
  min-height: 80%; /* Garante que o conteúdo tenha uma altura mínima */
  display: flex;
  flex-direction: column; /* Organiza os itens de forma vertical */
  justify-content: center; /* Centraliza os itens dentro do container */
}
