/* TaskInput.module.css */
.container {
  display: flex;
  align-items: center;
  justify-content: space-between;
  padding: 12px;
  background-color: #1a1a1d; /* Fundo mais escuro */
  border-radius: 12px;
  box-shadow: 0 4px 6px rgba(0, 0, 0, 0.1); /* Leve sombra */
}

.input {
  width: 80%;
  height: 40px;
  border-radius: 10px;
  border: 1px solid #3e1671;
  padding: 0 10px;
  font-size: 16px; /* Aumenta o tamanho da fonte */
  color: white;
  background-color: #2a2a2d; /* Fundo mais escuro para o campo de input */
  transition: border-color 0.2s ease;
}

.input:focus {
  border-color: #9e78cf; /* Cor de borda mais clara ao focar */
}

.button {
  width: 45px;
  height: 45px;
  background-color: #9e78cf;
  border-radius: 50%;
  color: white;
  font-size: 24px; /* Botão um pouco maior */
  display: flex;
  justify-content: center;
  align-items: center;
  cursor: pointer;
  transition: transform 0.2s ease, background-color 0.2s ease;
}

.button:hover {
  background-color: #5e56c9; /* Cor mais clara ao passar o mouse */
  transform: scale(1.1); /* Leve aumento no botão ao passar o mouse */
}
