/* TaskItem.module.css */
.container {
  display: flex;
  align-items: center;
  justify-content: space-between;
  background-color: #9e78cf; /* Um tom mais escuro */
  border-radius: 12px;
  padding: 12px 16px;
  margin-bottom: 12px;
  box-shadow: 0 4px 6px rgba(0, 0, 0, 0.2); /* Adiciona um leve sombreado */
}

.task {
  color: white;
  font-size: 18px; /* Tamanho de fonte um pouco maior */
  font-weight: 500; /* Font weight mais visível */
  flex-grow: 1; /* Permite que a tarefa ocupe o restante do espaço */
}

.completed {
  text-decoration: line-through;
  color: #78cfb0; /* Mantém a cor de completado */
}

.actions button {
  background-color: transparent;
  border: none;
  cursor: pointer;
  color: #9e78cf;
  font-size: 24px; /* Aumenta o tamanho do botão para facilitar a interação */
  margin-left: 16px;
  transition: transform 0.2s ease, color 0.2s ease; /* Suaviza a transição */
}

.actions button:hover {
  color: #5e56c9; /* Alterar para uma cor mais suave ao passar o mouse */
  transform: scale(1.1); /* Suaviza o efeito de foco */
}
